//vue router 가져오기
import { createWebHistory, createRouter } from "vue-router";
import ListVue from "./components/ListVue.vue";
import AboutVue from "./components/AboutVue.vue";
import DetailVue from "./components/DetailVue.vue";
import AuthorVue from "./components/AuthorVue.vue";
import CommentVue from "./components/CommentVue.vue";

const routes = [
  {
    path: "/list",
    component: ListVue,
  },
  {
    path: "/",
    component: AboutVue,
  },
  {
    path: "/detail/:id",
    component: DetailVue,
    children: [
      {
        path: "author",
        component: AuthorVue,
      },
      {
        path: "comment",
        component: CommentVue,
      },
    ],
  },
  // 404 페이지
  // {
  //   path: "/:aniything(.*)",
  //   component: AboutVue,
  // },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
